body {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-style: normal;
  line-height: 1.5; }

strong,
h1,
h2,
h3,
h4 {
  font-weight: bold;
  font-style: normal; }

.small {
  font-size: 0.6em;
  line-height: 1.8; }

.wrapper {
  padding: 10px;
  min-height: calc(100vh - 20px); }
  @media (min-width: 768px) {
    .wrapper {
      padding: 40px 0;
      min-height: calc(100vh - 80px); } }

.dateWrapper {
  width: 136px; }
  @media (min-width: 768px) {
    .dateWrapper {
      width: 220px; } }

.date {
  font-size: 25px;
  display: block; }
  .date__subtitle {
    font-size: 18px;
    line-height: 1; }

@media (min-width: 768px) {
  .date {
    font-size: 40px; }
    .date__subtitle {
      font-size: 28px; } }

main {
  max-width: 394px;
  position: relative;
  margin: 0 auto;
  display: flex;
  align-items: center;
  padding: 10px;
  min-height: 736px; }
  @media (min-width: 768px) {
    main {
      width: 90vw;
      max-width: 1440px;
      height: 880px;
      padding: 0; } }

.content {
  padding: 20px 0; }
  @media (min-width: 768px) {
    .content {
      width: 620px;
      padding: 40px; } }

h1 {
  font-size: 1.2em; }
  @media (min-width: 768px) {
    h1 {
      font-size: 1.9em; } }

.email {
  width: 90%;
  border: solid #000 2px; }
  @media (min-width: 768px) {
    .email {
      width: 436px;
      float: left; } }

@media (min-width: 768px) {
  .emailWrapper {
    display: flex;
    align-items: center; } }

.emailOptinLabel {
  width: 80%; }
  @media (min-width: 768px) {
    .emailOptinLabel {
      width: 94%; } }

button {
  margin-top: 20px;
  background: #EA3F33;
  border: none;
  padding: 10px 40px;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer; }

button:disabled {
  opacity: 0.5; }

.breakWrapper {
  width: 100%;
  height: 736px;
  max-width: 1440px;
  margin: 0 auto; }
  @media (min-width: 768px) {
    .breakWrapper {
      height: 900px; } }
